import React, {useState} from 'react';
import '../App.css';

const ConfirmPurchageForm = ({ userNow, setPage, setUserdata }) => {
    const [firstname, setFirstname] = useState("")
    const [secondname, setSecondname] = useState("")
    const [email, setEmail] = useState("")
    const [bankcard, setBankcard] = useState("")
    const [monthyears, setMonthyears] = useState("")
    const [cvv, setCvv] = useState("")
    const [adress, setAdress] = useState("")

    const submitHandler = (e) => {
        console.log('s')
        e.preventDefault();
        e.stopPropagation();
        setUserdata(
            {
                firstname: firstname,
                secondname: secondname,
                email: email,
                bankcard: bankcard,
                monthyears: monthyears,
                cvv: cvv,
                adress: adress
            }
        );
        setPage("summary") }
        return (
            <div>
                <form onSubmit={r => submitHandler(r)}>
                    <label className="normaltext" name="firstname">Etunimi: </label>
                    <input onChange={e => setFirstname(e.target.value)} type="text" name="firstname" value={firstname} />
                    <br></br>
                    <label className="normaltext" name="forename">Sukunimi: </label>
                    <input onChange={e => setSecondname(e.target.value)} type="text" name="forename" value={secondname} />
                    <br></br>
                    <label className="normaltext" name="email">Sähköposti: </label>
                    <input onChange={e => setEmail(e.target.value)} type="text" name="email" value={email} />
                    <br></br>
                    <label className="normaltext" name="adress">Osoite </label>
                    <input onChange={e => setAdress(e.target.value)} type="text" name="adres" value={adress} />
                    <br></br>
                    <label className="normaltext" name="bankcard">Pankkikortti: </label>
                    <input onChange={e => setBankcard(e.target.value)} type="text" name="bankcard" value={bankcard} />
                    <br></br>
                    <label className="normaltext" name="date">MM/YY: </label>
                    <input onChange={e => setMonthyears(e.target.value)} type="text" name="date" value={monthyears} />
                    <br></br>
                    <label className="normaltext" name="cvv">CVV: </label>
                    <input onChange={e => setCvv(e.target.value)} type="text" name="cvv" value={cvv} />
                    <br></br>
                    <button type="submit"> Vahvistaa maksu </button>
                </form>
            </div>
        )
 
    }

    export default ConfirmPurchageForm;