import React, { useState } from 'react'
import ConfirmPurchageForm from './ConfirmPurchageForm'

const Summary = ({ products, selected, setproducts, setPage, userdata }) => {
  
    const filteredproducts = products.filter(product =>
        product.amount > 0)

        const setToZeros = () => {
            const tempProducts = products.map(product => {
                product = { ...product, amount: 0 };
                return product;
               
            });
            setproducts(tempProducts);
        }

        const cartTotal = () => {
            let total = 0;
            if (products.length !== 0) {
                const productsArray = products.map(product => product.price * product.amount);
                console.log(productsArray);
                total = productsArray.reduce((a, b) => a + b, 0);
            }
            return total;
        }

return (
    <div className="kuva">
        {filteredproducts.map(product =>
            <div key={product.id}>
        <p> Kuvaus:  {product.description} <br></br>
            Otsikko: {product.title} <br></br>
            Hinta: {product.price}€ <br></br>
            Määrä:  {product.amount}kpl <br></br>
            </p>
            </div>
        )}
         Etunimi: {userdata.firstname} <br></br>
            Sukunimi: {userdata.secondname} <br></br>
            Osoite: {userdata.adress} <br></br>
            Sähköpost: {userdata.email} <br></br>
            Pankkikortti: {userdata.bankcard} <br></br>
            MM/YY: {userdata.monthyears} <br></br>
            CVV: {userdata.cvv} <br></br>
          Koko Hinta: {cartTotal()} €
        <button onClick={e => setToZeros() }>Tyhjennä </button>
        <button onClick={e => setPage("cart") }>Palaa </button>
    </div>)
}


export default Summary;