import React, { useState } from 'react'


const ProductCart = ({ products, selected, setproducts, setPage }) => {
  
const filteredproducts = products.filter(product =>
    product.amount > 0)
const addOne = (id) => {
    const tempProducts = products.map(product => {
        if (product.id === id) {
            const tempProduct = { ...product, amount: (product.amount + 1) };
            return tempProduct;
        } else {
            return product;
        }
    })
    setproducts(tempProducts)
    console.log(products)
    console.log("lisätty", selected)
}

const minusOne = (id) => {
    const tempProducts = products.map(product => {
        if (product.id === id) {
            const tempProduct = { ...product, amount: (product.amount - 1) };
            return tempProduct;
        } else {
            return product;
        }
    })
    setproducts(tempProducts)
    console.log(products)
    console.log("lisätty", selected)
}

const cartTotal = () => {
    let total = 0;
    if (products.length !== 0) {
        const productsArray = products.map(product => product.price * product.amount);
        console.log(productsArray);
        total = productsArray.reduce((a, b) => a + b, 0);
    }
    return total;
}

const setToZeros = () => {
    const tempProducts = products.map(product => {
        product = { ...product, amount: 0 };
        return product;
    });
    setproducts(tempProducts);
}



return (
    <div className="kuva">
        {filteredproducts.map(product =>
            <div key={product.id}>
        <p> Kuvaus:  {product.description}
            Otsikko: {product.title}
            Hinta: {product.price}€
            Määrä:  {product.amount}kpl</p>  <button onClick={e => addOne(product.id)}>+ </button>
            <button onClick={e => minusOne(product.id)}>- </button>
            Hinta: { product.price * product.amount} €
            </div>
        )}
        {cartTotal() > 0 &&
        <div>
          Koko Hinta: {cartTotal()} €
        <button onClick={e => setToZeros()}>Tyhjennä </button>
        <button onClick={e => setPage("confirmpurchageform")}> Kassalle </button>
        </div>}
    </div>)
}


export default ProductCart;