import React from 'react'

const ProductDetails = ({ products, selected, setproducts }) => {

    const product = products.filter(product =>
        product.id === selected)
    const addOne = (id) => {
        const tempProducts = products.map(product => {
            if (product.id === id) {
                const tempProduct = { ...product, amount: (product.amount + 1) };
                return tempProduct;
            } else {
                return product;
            }
        })
        setproducts(tempProducts)
        console.log(products)
        console.log("lisätty", selected)
    }
    console.log(product)

    return (
        <div className="kuva">
            <img className="funnel" src={product[0].url} alt="muki" />
            <p> Kuvaus:  {product[0].description} </p>
            <p> Otsikko: {product[0].title} </p>
            <p> Hinta: {product[0].price}€ </p>
            <p> Määrä:  {product[0].amount}kpl</p>
            <button onClick={e => addOne(product[0].id)}>Lisää koriin </button>
        </div>
    )
}
export default ProductDetails;
