import React, {useState} from 'react';
import logo from './logo.svg';
import './App.css';
import MyCaroussel from './components/MyCaroussel'
import images from './components/image_data.js'
import MyNavBar from './components/MyNavbar.js'
import ProductDetails from './components/ProductDetails';
import ProductCart from './components/ProductCart'
import ConfirmPurchageForm from './components/ConfirmPurchageForm'
import Summary from './components/Summary'


const App = () => {
  const links = [{id:1, description: "Linkkivinkki 1", url: "http://is.fi"}, 
 {id:2, description: "Linkkivinkki 2", url: "http://hs.fi"}];
 const [page, setPage] = useState("cart")
 const [userdata, setUserdata] = useState({})
 const [selected, setselected] = useState(0)
 const [products, setproducts] = useState(images)
console.log(selected)
  return (
    <div className="App">
      <header className="App-header">
        <h1>Fanikauppa</h1>
      </header>
      <nav>
        <MyNavBar links={links}/>
      </nav>
      <section className="home">
  <MyCaroussel className="carousel" images={images} setselected={setselected} selected={selected}/>
  <ProductDetails products={products} selected={selected} setproducts={setproducts} />
  
      </section>
      <div className="page-container">
         <section className="content-wrap">
             {page === "cart" && <ProductCart products={products} setPage={setPage} selected={selected} setproducts={setproducts} />}
             {page === "confirmpurchageform" && <ConfirmPurchageForm  setPage={setPage} setUserdata={setUserdata} />}
             {page === "summary" && <Summary setPage={setPage} userdata={userdata} products={products} setproducts={setproducts} />}
         </section>
     </div>
    </div>
  );
}

export default App;
